body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}
body {
  background: linear-gradient(#141e30, #243b55);
  color: whitesmoke;
  font-family: -apple-system, 'Droid Sans', sans-serif;
}
.container {
  border: 1px solid #d2d2d2;
  border-radius: 6px;
  box-shadow: 0px 1px 6px -2px #807f7f;
  margin: 16px 8px;
  padding: 16px;
  background: linear-gradient(#141e30, #243b55);
}
.container.form {
  display: flex;
  align-items: baseline;
}
@media (max-width: 650px) {
  .container.form {
    flex-direction: column;
  }
}
.container.form .form-group {
  padding: 0 24px 0 24px;
  display: flex;
  flex-direction: column;
}
.container.form .form-group.submit {
  flex: 2;
  align-items: flex-start;
}
.container.form .form-group.inputs {
  flex: 2;
  color: #a59b9b;
}
.container.form .form-group.checkboxes {
  flex: 1;
  color: #a59b9b;
}
a {
  font-size: 0.75em;
}
header {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
}
h1,
h2,
h3,
h4 {
  color: #877ceb;
}
form h2,
form h4,
form h4 {
  color: #52f3e6;
}
label {
  display: flex;
  margin-bottom: 16px;
  justify-content: space-between;
}
input[type=text],
select {
  height: 1.5rem;
}
button {
  height: 2rem;
  width: 6rem;
  border-radius: 8px;
  color: #1edc14;
  background-color: #0e0d0d;
  border: 2px solid #1edc14;
}
button:hover {
  cursor: pointer;
}
button:disabled {
  color: crimson;
  background-color: white;
  border: 2px solid crimson;
  cursor: not-allowed;
}
.errors {
  margin: 8px;
  font-size: 0.7em;
  color: red;
}
.errors div {
  display: block;
  margin-top: 4px;
}


